export function sumBy(array, getNumber) {
    let sum = 0;
    for (let a of array) {
        sum += getNumber(a);
    }
    return sum;
}
export function productBy(array, getNumber) {
    let product = 1;
    for (let a of array) {
        product *= getNumber(a);
    }
    return product;
}
export function orElse(maybe, alternative) {
    return (maybe.kind === 'Just') ? maybe.value : alternative;
}
export function maxBy(array, getNumber) {
    if (array.length === 0) {
        return { kind: 'Nothing' };
    }
    let max = array[0];
    for (let a of array) {
        max = (getNumber(a) > getNumber(max)) ? a : max;
    }
    return { kind: 'Just', value: max };
}
export function minBy(array, getNumber) {
    if (array.length === 0) {
        return { kind: 'Nothing' };
    }
    let max = array[0];
    for (let a of array) {
        max = (getNumber(a) < getNumber(max)) ? a : max;
    }
    return { kind: 'Just', value: max };
}
export function range(n) {
    let retVal = [];
    for (let i = 0; i < n; i++) {
        retVal.push(i);
    }
    return retVal;
}
export function endSwitch(x) {
    throw Error('Shouldn\'t get here.');
}
export function just(value) {
    return { kind: 'Just', value: value };
}
export function nothing() {
    return { kind: 'Nothing' };
}
export function twoPointForm(point1, point2) {
    const m = slope(point1, point2);
    return (x) => m * (x - point1.x) + point1.y;
}
export function slope(point1, point2) {
    return (point2.y - point1.y) / (point2.x - point1.x);
}
export function point(x, y) {
    return { x: x, y: y };
}
